@import url('https://fonts.googleapis.com/css?family=Permanent Marker + PT Serif');

body{
  margin: 0;
  padding: 0;
  min-height: 800px;
  background-color: bisque;
  
}

.App{
  text-align: center;
}
.heading{
  background-color: rgb(109, 165, 243);
  color:black;
  width: 800px;
  font-family: 'Permanent Marker', cursive;
  font-size: 70px;
  font-weight: 600;
  margin-left: 300px;
  height: 100px;
  border-radius: 10px;
}
.inputBox{
  
  margin-top: 10px;
}
.addTaskButton{
  height: 80px;
  width: 80px;
  border-radius: 100%;
  margin-left: 20px;
  color: white;
  font-weight:900;
  font-size: 20px;
  background-color: black;
} 
.input{
  
  border-radius: 10px;
  text-align: center;
  width: 400px;
  height: 80px;
  background-color: rgb(22, 21, 21);
  color: white;
  font-size: 30px;
  font-family: 'PT Serif', serif;
}

h2{
  color: black;
  font-size:xx-large;
  margin-left: 150px;
  text-align: center;
  font-family: 'PT Serif', serif;
}
.show{
  background-color: black;
  border-radius: 10px;
  width: 600px;
  margin-left: 350px;
  margin-top: 100px;
  text-align: center;
}
.deleteButton{
  width: 120px;
  background-color: black;
  color: white;
  font-size: 30px;
  border-radius: 10px;
  height: 50px ;
  margin-bottom: 30px;
  margin-top: 30px;
  text-align: center;
  margin-left: 20px;
  font-family: 'PT Serif', serif;
}
.done{
  width: 100px;
  background-color: black;
  color: white;
  font-size: 30px;
  border-radius: 10px;
  height: 50px ;
  margin-bottom: 30px;
  margin-top: 30px;
  text-align: center;
  margin-left: 20px;
  font-family: 'PT Serif', serif;

}